.game{
    width:100%;
    background-color: #282c34;

}
.game-board{
    margin :0;
    padding:0;
    display:inline-block;

}
.game-message{
    vertical-align:top;
    display:inline-block;   
    margin-top:-10px;
    margin-left:10px;
    position:relative;
    background-color: antiquewhite;
}

.game-info{
    vertical-align:top;
    display:inline-block;   
    margin-top:-10px;
    margin-left:10px;
    position:relative;
    width : 75vmin;
}

.game-analysis{
    color:white;
    position: absolute;
    left: 20vmin;
    top: 20px;
    color:white;
}

@media screen and (min-width: 1200px) {
    .game-info{
        vertical-align:top;
        display:inline-block;   
        margin-top:-10px;
        margin-left:10px;
        position:relative;
        width : 55vmin;
        overflow:hidden;
    }
  }

  @media screen and (min-width: 1300px) {
    .game-analysis{
        position: absolute;
        left: 70vmin;
        top: 25px;
        color:white;
    }
  }

  @media screen and (min-width: 1400px) {
    .game-analysis{
        position: absolute;
        left: 140vmin;
        top: 175px;
        color:white;
    }
  }